.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.container {
  width: 70%;
}

.color-blue {
  color: #004BA8;
}

.color-red {
  color: #B33F62
}

.color-delete {
  background-color: #B33F62;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form-container {
  width: 60%;
  margin: 0 auto;
}